        needMoney: ""
      },
      zhuandan: false,
      imgDiv: false,
      imgUrl: "",
      isSend: false,
      claimDiv: false,
      close: false,
      prePayFilesList: null,
      depositFilesList: null,
      balanceFilesList: null,
      addUpload: false,
      addUpload2: false,
      addUpload3: false,
      addUploadSort: "",
      addUploadSort2: "",
