      prePayMoney2: "",
      backM: "",
      compM: "",
      addZipDiv:false,
      files1:[],
      files2:[],
      files3:[],
      files:"",
      fileName:"",
      zipSort:"",
      imgArr:[],
      imgArrName:[],
      checkAll: false,
      checkAll2: false,
      checkAll3: false,
      ischecked:true,
      files1a:[],
      files2a:[],
      files3a:[],
      filesNum:1
    };
  },
  created() {
    this.getDetail();
    this.getClaim();
  },
  components: {
    UserAddress
  },
  mounted() {},
  methods: {
      handleCheckAllChange(val) {
        this.files1 = val?this.files1a:[];
      },
      handleCheckedCitiesChange(value) {
        if(this.files1.length==this.files1a.length){
           this.checkAll=true
        }else{
          this.checkAll=false;
        }
      },
      handleCheckAllChange2(val) {
        this.files2 = val?this.files2a:[];
      },
      handleCheckedCitiesChange2(value) {
        if(this.files2.length==this.files2a.length){
           this.checkAll2=true
        }else{
          this.checkAll2=false;
        }
      },
      handleCheckAllChange3(val) {
        this.files3 = val?this.files3a:[];
      },
      handleCheckedCitiesChange3(value) {
        if(this.files3.length==this.files3a.length){
           this.checkAll3=true
        }else{
          this.checkAll3=false;
        }
      },
    addZip(type){
      if(type=='1'){
         if(this.files1.length>0){
           this.addZipDiv=true;
           this.zipSort='1';
           this.fileName=''
         }else{
           this.$message("");
         }
      }else if(type=='2'){
         if(this.files2.length>0){
           this.addZipDiv=true;
           this.zipSort='2';
           this.fileName=''
         }else{
           this.$message("");
         }
      }else{
         if(this.files3.length>0){
           this.addZipDiv=true;
           this.zipSort='3';
           this.fileName=''
         }else{
           this.$message("");
         }
      }
    },
    submitZip(){
       if(this.zipSort=='1'){
         this.files=this.files1;
       } else if(this.zipSort=='2'){
         this.files=this.files2;
       } else if(this.zipSort=='3'){
         this.files=this.files3;
       }
       let that=this;
       var zip = new JSZip();
       that.addZipDiv=false;
       const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        }); 
        that.filesNum=1;

       for(let i=0;i<that.files.length;i++){
          let imgLink = that.files[i].split(';')[0];
          let imgName = that.files[i].split(';')[1];
          let tempImage = new Image();
            tempImage.crossOrigin = "*";
            tempImage.onload = function(){
                let base64 = that.getBase64Image(tempImage);
                that.imgArr[i]=base64.split(",")[1];
                that.imgArrName[i]=imgName;
                that.filesNum=that.filesNum+1;
            }
            tempImage.src = imgLink;
       }
       var myVar = setInterval(() => {
              if(that.filesNum==(that.files.length+1)){
                    for(var a=0;a<that.files.length;a++){
                      zip.file(that.imgArrName[a]+'.jpg', that.imgArr[a], {base64: true});
                    }
                    
                        zip.generateAsync({type:"blob"})
                        .then(function(content) {
                            saveAs(content, that.fileName+".zip");
                          });
                        loading.close();
                        clearInterval(myVar);
                   
                }
        }, 1000);

       
      //  setTimeout(()=>{
      //     for(var i=0;i<that.files.length;i++){
      //       zip.file(that.imgArrName[i]+'.jpg', that.imgArr[i], {base64: true});
      //     }
      //     zip.generateAsync({type:"blob"})
      //     .then(function(content) {
      //         saveAs(content, that.fileName+".zip");
      //       });
      //     loading.close();
      //  },2000)
    },
    getBase64Image(img) {
            var canvas = document.createElement("canvas");
            canvas.width = img.width;
            canvas.height = img.height;
            var ctx = canvas.getContext("2d");
            ctx.drawImage(img, 0, 0, img.width, img.height);
            var ext = img.src.substring(img.src.lastIndexOf(".")+1).toLowerCase();
            var dataURL = canvas.toDataURL("image/"+ext);
            return dataURL;
        },
    getDetail() {
      this.axios
        .get("/productOrder/" + this.$route.query.id)
        .then(res => {
          if (res.data.code === 200) {
            if (res.data.data.groundPersonListVO) {
              this.detail.groundPersonListVO = res.data.data.groundPersonListVO;
            }
            if (res.data.data.orderFlow) {
              this.detail.orderFlow = res.data.data.orderFlow;
              if (res.data.data.orderFlow.prePayFiles) {
                this.prePayFilesList = res.data.data.orderFlow.prePayFiles.split(
                  ";"
                );
                for (var i = 0; i < this.prePayFilesList.length; i++) {
                  this.prePayFilesList[i] = this.prePayFilesList[i].split(",");
                }
              }
              if (res.data.data.orderFlow.depositFiles) {
                this.depositFilesList = res.data.data.orderFlow.depositFiles.split(
                  ";"
                );
                for (var i = 0; i < this.depositFilesList.length; i++) {
                  this.depositFilesList[i] = this.depositFilesList[i].split(
                    ","
                  );
                }
              }
              if (res.data.data.orderFlow.balanceFiles) {
                this.balanceFilesList = res.data.data.orderFlow.balanceFiles.split(
                  ";"
                );
                for (var i = 0; i < this.balanceFilesList.length; i++) {
                  this.balanceFilesList[i] = this.balanceFilesList[i].split(
                    ","
                  );
                }
              }

              if (res.data.data.orderFlow.prePayMoney) {
                var arry = res.data.data.orderFlow.prePayMoney.split(",");
                this.prePayMoney1 = arry[0];
                this.addPayList = arry.slice(1);
              }
              this.claimMain.orderFlowId = res.data.data.orderFlow.id;
            }
            this.detail.orderListVO = res.data.data.orderListVO;
            this.detail.productId = res.data.data.productId;
            this.detail.productName = res.data.data.productName;
            this.detail.template = res.data.data.template;
            if(res.data.data.personNames){
              this.detail.personNames = res.data.data.personNames;
            }
            
            this.prePayMoney2 = "";
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    sendAddPay() {
      if (this.prePayMoney1 != "") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.detail.orderFlow.refusePay = false;
            this.submit("pay");
            this.isSend = false;
            setTimeout(() => {
              this.postClaim();
            }, 2000);
          })
          .catch(() => {});
      } else {
        this.$message("");
      }
    },
    sendAddPay2() {
      if (this.prePayMoney2 != "") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.submit("pay");
            this.isSend = false;
            setTimeout(() => {
              this.postClaim();
            }, 2000);
          })
          .catch(() => {});
      } else {
        this.$message("");
      }
    },
    getClaim() {
      this.axios
        .get("/claim?productOrderId=" + this.$route.query.id)
        .then(res => {
          if (res.data.code === 200) {
            if (res.data.data) {
              this.claimMain = res.data.data;
              if (res.data.data.prePayTimes) {
                this.claimMain.prePayTimes = res.data.data.prePayTimes.split(
                  ","
                );
              }
            }
          }
        })
        .catch(err => {
          console.log(err);
          this.$message(this.mes.busy);
        });
    },
    closeSubmit() {
      this.axios
        .delete(
          "/productOrder/" + this.$route.query.id + "?reason=" + this.reason
        )
        .then(res => {
          if (res.data.code === 200) {
            this.$message({
              type: "success",
              message: "!"
            });
            this.$router.back(-1);
          }
        })
        .catch(err => {
          console.log(err);
          this.$message(this.mes.busy);
        });
    },
    intPerson() {
      this.searchForm.province = "";
      this.searchForm.city = "";
      this.searchForm.country = "";
      this.api
        .get("/groundPerson?" + queryString.stringify(this.searchForm))
        .then(res => {
          if (res.code == 200) {
            this.personList = res.data.list;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getPerson() {
      let addressParams = this.$refs.AddAddress.getData();
      this.searchForm.province = addressParams.province;
      this.searchForm.city = addressParams.city;
      this.searchForm.country = addressParams.country;
      this.api
        .get("/groundPerson?" + queryString.stringify(this.searchForm))
        .then(res => {
          if (res.code == 200) {
            this.personList = res.data.list;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    zhuandanSubmit() {
      this.api
        .put(
          "/productOrder/" + this.$route.query.id + "/groundPerson",
          this.form,
          "form"
        )
        .then(res => {
          if (res.code == 200) {
            this.$message("");
            this.zhuandan = false;
            this.getDetail();
            this.getClaim();
          }
        })
        .catch(res => {
          console.log(res);
        });
    },
    imgLook(url) {
      this.imgDiv = true;
      this.imgUrl = url;
    },
    back() {
      this.$router.back(-1);
    },
    postClaim() {
      delete this.claimMain.createTime;
      delete this.claimMain.realPayMoney;
      delete this.claimMain.updateTime;

      if (this.claimMain.backMoney == null) {
        delete this.claimMain.backMoney;
      }
      if (this.claimMain.compensateMoney == null) {
        delete this.claimMain.compensateMoney;
      }

      if (this.claimMain.prePayTimes) {
        this.claimMain.prePayTimes =
          this.claimMain.prePayTimes + "," + new Date().valueOf();
      } else {
        this.claimMain.prePayTimes = new Date().valueOf();
      }
      let total = this.detail.orderFlow.prePayMoney.split(",");
      this.claimMain.totalMoney = total.reduce(function(
        preValue,
        curValue,
        index,
        array
      ) {
        return Number(preValue) + Number(curValue);
      });

      if (this.claimMain.backMoney) {
        this.backM = Number(this.claimMain.backMoney);
      } else {
        this.backM = Number(0);
      }
      if (this.claimMain.compensateMoney) {
        this.compM = Number(this.claimMain.compensateMoney);
      } else {
        this.compM = Number(0);
      }
      this.claimMain.needMoney =
        Number(this.claimMain.totalMoney) - this.backM - this.compM;

      if (this.claimMain.id) {
        this.api
          .put("/claim", this.claimMain, "form")
          .then(res => {
            this.getClaim();
          })
          .catch(err => {
            console.log(err);
            this.$message(err.data.message);
          });
      } else {
        this.api
          .post("/claim", this.claimMain, "form")
          .then(res => {
            this.getClaim();
          })
          .catch(err => {
            console.log(err);
            this.$message(err.data.message);
          });
      }
    },
    claim() {
      delete this.claimMain.createTime;

      if (this.claimMain.prePayTimes == null) {
        delete this.claimMain.prePayTimes;
      }
      if (this.claimMain.totalMoney == null) {
        delete this.claimMain.totalMoney;
      }
      delete this.claimMain.realPayMoney;
      delete this.claimMain.updateTime;

      if (this.claimMain.backMoney == null) {
        this.claimMain.backMoney = "0";
      }
      if (this.claimMain.compensateMoney == null) {
        this.claimMain.compensateMoney = "0";
      }
      if (this.claimMain.totalMoney) {
        this.claimMain.needMoney =
          Number(this.claimMain.totalMoney) -
          Number(this.claimMain.backMoney) -
          Number(this.claimMain.compensateMoney);
      }

      if (this.claimMain.id) {
        this.api
          .put("/claim", this.claimMain, "form")
          .then(res => {
            this.claimDiv = false;
          })
          .catch(err => {
            console.log(err);
            this.claimDiv = false;
            this.$message(err.data.message);
          });
      } else {
        this.api
          .post("/claim", this.claimMain, "form")
          .then(res => {
            this.claimDiv = false;
          })
          .catch(err => {
            console.log(err);
            this.claimDiv = false;
            this.$message(err.data.message);
          });
      }
    },
    fileChange(e) {
      FileChange(e, res => {
        this.addUploadUrl = res.url;
      });
    },
    fileChange2(e) {
      FileChange(e, res => {
        this.addUploadUrl2 = res.url;
      });
    },
    fileChange3(e) {
      FileChange(e, res => {
        this.addUploadUrl3 = res.url;
      });
    },
    addUploadSubmit() {
      if (this.addUploadSort) {
        let i = Number(this.addUploadSort);
        if(this.prePayFilesList){
            if (this.prePayFilesList[i][0] == "") {
              this.prePayFilesList[i][0] = this.addUploadUrl;
            } else {
              this.prePayFilesList[i] = this.prePayFilesList[i].concat(
                this.addUploadUrl
              );
            }
        }else{
          this.prePayFilesList=["","","","","","","","","","","","","","",""];
          this.prePayFilesList[i] = this.addUploadUrl;
        }
        
        this.addUpload = false;
        this.submit();
      } else {
        this.$message("");
      }
    },
    addUploadSubmit2() {
      if (this.addUploadSort2) {
        let i = Number(this.addUploadSort2);
        if(this.depositFilesList){
            if (this.depositFilesList[i][0] == "") {
              this.depositFilesList[i][0] = this.addUploadUrl2;
            } else {
              this.depositFilesList[i] = this.depositFilesList[i].concat(
                this.addUploadUrl2
              );
            }
         }else{
          this.depositFilesList=["","","",""];
          this.depositFilesList[i] = this.addUploadUrl2;
        }

        this.addUpload2 = false;
        this.submit();
      } else {
        this.$message("");
      }
    },
    addUploadSubmit3() {
      if (this.addUploadSort3) {
        let i = Number(this.addUploadSort3);
        if(this.balanceFilesList){
            if (this.balanceFilesList[i][0] == "") {
              this.balanceFilesList[i][0] = this.addUploadUrl3;
            } else {
              this.balanceFilesList[i] = this.balanceFilesList[i].concat(
                this.addUploadUrl3
              );
            }
        }else{
          this.balanceFilesList=["","","","","","","","","",""];
          this.balanceFilesList[i] = this.addUploadUrl3;
        }
        this.addUpload3 = false;
        this.submit();
      } else {
        this.$message("");
      }
    },
    postWx(id){
      this.api
          .post("/orderFlow/stateChange", {state:id,productOrderId:this.$route.query.id}, "form")
          .then(res => {
            console.log(res)
          })
          .catch(err => {
            console.log(err);
            this.$message(err.data.message);
          });
    },
    submit(pay) {
      let editOrder = {
        //prePayFiles: this.prePayFilesList.join(";"),
        //prePayFileConfirm: this.detail.orderFlow.prePayFileConfirm,
        //prePayMoney: this.detail.orderFlow.prePayMoney,
        //refusePay: this.detail.orderFlow.refusePay,
        //depositFiles: this.depositFilesList.join(";"),
        //refuseDeposit: this.detail.orderFlow.refuseDeposit,
        //balanceFiles: this.balanceFilesList.join(";"),
        //refuseBalance: this.detail.orderFlow.refuseBalance,
        //finishNeedMoney: this.detail.orderFlow.finishNeedMoney,
        //id: this.detail.orderFlow.id,
        productOrderId: this.detail.orderFlow.productOrderId
      };
      if (this.prePayFilesList) {
        editOrder.prePayFiles = this.prePayFilesList.join(";");
      }
      if (this.depositFilesList) {
        editOrder.depositFiles = this.depositFilesList.join(";");
      }
      if (this.balanceFilesList) {
        editOrder.balanceFiles = this.balanceFilesList.join(";");
      }
      if (this.detail.orderFlow.id) {
        editOrder.id = this.detail.orderFlow.id;
      }
      if (pay == "pay") {
        if (this.detail.orderFlow.prePayMoney != null) {
          editOrder.prePayMoney =
            this.detail.orderFlow.prePayMoney + "," + this.prePayMoney2;
        } else {
          editOrder.prePayMoney = this.prePayMoney1;
        }
      } else {
        delete editOrder.prePayMoney;
      }

      if (this.detail.orderFlow.finishNeedMoney != null) {
        editOrder.finishNeedMoney = this.detail.orderFlow.finishNeedMoney;
      }

      if (this.detail.orderFlow.prePayFileConfirm != null) {
        editOrder.prePayFileConfirm = this.detail.orderFlow.prePayFileConfirm;
      }
      if (this.detail.orderFlow.refusePay != null) {
        editOrder.refusePay = this.detail.orderFlow.refusePay;
      }
      if (this.detail.orderFlow.refuseDeposit != null) {
        editOrder.refuseDeposit = this.detail.orderFlow.refuseDeposit;
      }
      if (this.detail.orderFlow.refuseBalance != null) {
        editOrder.refuseBalance = this.detail.orderFlow.refuseBalance;
      }
      this.api
        .put("/productOrder/orderFlow", editOrder, "form")
        .then(res => {
          this.$message("");
          this.getDetail();
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  filters: {
    formatDate: function(time) {
      if (time) {
        let date = new Date(time);
        return ScondDay(time);
      } else {
        return "";
      }
    },
    formatDate2: function(time) {
      if (time) {
        var date = new Date(time);
        var Y = date.getFullYear() + "-";
        var M =
          (date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1) + "-";
        var D =
          (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
        var h =
          (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) +
          ":";
        var m =
          (date.getMinutes() < 10
            ? "0" + date.getMinutes()
            : date.getMinutes()) + ":";
        var s =
          date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
        return Y + M + D + h + m + s;
      } else {
        return "";
      }
    }
  }
};
</script>

